* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Jost", sans-serif;
}
a {
  text-decoration: none;
}

li {
  list-style: none;
}
 
/* navbar style start  */

.main-nav {
  width: 100%;
  height: 5rem;
  background-color: #fff;
  display: grid;
  grid-template-columns: 10rem 1fr 2fr 1fr 10rem;
  position: fixed;
  z-index: 100;
}
.logoDiv{
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.menu-link {
  grid-column: 3/4;
}

.menu-link ul {
  height: 5rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.social-media ul {
  height: 5rem;
  display: grid;
  grid-template-columns: 3fr repeat(3, 1fr);
  align-items: center;
  justify-content: flex-end;
}

.social-media ul li {
  text-align: right;
}

.social-media ul li:first-child {
  grid-column: 2/3;
}

/* ----------- Grid part ends ----------------- */

.navLink {
  text-transform: uppercase;
  color: #000;
  transition: 0.5s;
  font-size: 0.8rem;
  font-weight: bold;
  letter-spacing: 3px;
  font-family: 'Manrope', sans-serif;
}
.navLink:hover{
  color: #a76263;
}
.social-media .hamburger-menu {
  display: none;
}

.facebook {
  color: #3b5998;
  width: 30px;
  height: 30px;
}

.instagram {
  color: #c32aa3;
  width: 30px;
  height: 30px;
}

.youtube {
  color: #ff0000;
  width: 30px;
  height: 30px;
}
.dropDown ul{
  display: flex;
  flex-direction: column;
  background-color: #000;
  padding: 2%;
  position: absolute;
  top: 100%;
  border-radius: 2px;
  box-shadow: 5px 5px 15px #888;
  /* visibility: hidden; */
}
.dropDownLi{
  color: #fff;
}

/* responsive css style  */
@media (max-width: 1080px) {
  .navLink{
    color: #fff;
  }
  .main-nav {
    height: 8rem;
    /* background-color: #fff; */
    grid-template-columns: 2rem 3fr 3fr 1fr 2rem;
  }

  .menu-link ul,
  .social-media ul {
    height: 8rem;
  }
}

/* responsive css style  */
@media (max-width: 998px) {
  .main-nav {
    height: 6rem;
    /* grid-template-columns: 2rem 2fr 3fr 2rem 2rem; */
  }
  .logo img{
    width: 8%;
    position: absolute;
    top: 5px;
  }
  .menu-link {
    display: none;
  }

  .social-media ul {
    height: 7rem;
  }

  .mobile-menu-link {
    grid-column: 2/4;
    position: relative;
    z-index: 99;
  }

  .mobile-menu-link {
    background-color: #000;
    height: auto;
    display: grid;
    grid-column: 2/5;
    align-items: center;
    padding-left: 3rem;
    transition: all 2s linear;
    -webkit-transform-origin: top;
            transform-origin: top;
    position: absolute;
    top: 100%;
    width: 100%;
  }

  .mobile-menu-link ul {
    height: 10rem;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: start;
  }

  .mobile-menu-link ul li:first-child {
    transition-delay: 0.2s;
  }

  .social-media {
    grid-row: 1/2;
    grid-column: 3/5;
    justify-items: end;
    align-items: center;
    transition: all 2s linear;
  }

  .social-media .social-media-desktop {
    height: 0;
    display: none;
  }

  .social-media {
    height: 7rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }
}

@media (max-width: 798px) {
  .main-nav {
    height: 5rem;
    grid-template-columns: 1rem 2fr 1fr 1fr 1rem;
  }

  .social-media ul {
    height: 6rem;
  }
  .logo img{
    width: 8%;
    position: absolute;
    top: 10px;
  }
  .social-media {
    height: 6rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2rem;
  }
}

@media (max-width: 520px) {
  .main-nav {
    height: 2rem;
    grid-template-columns: 1rem 4fr 1fr 1fr 1rem;
  }

  .social-media ul {
    height: 6rem;
  }
  .social-media {
    height: 6rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 1rem;
    position: absolute;
    top: 8px;
    right: 8px;
  }
  .logo img{
    width: 5%;
    position: absolute;
    top: 2px;
  }

  /* hero section  */

  .hero-section h1 {
    font-size: 3.8rem;
  }
