@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap');
.productDiv div{
    display: flex;
}

.mainModal{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 4%;
}
.childModal{
    padding: 2%;
    cursor: pointer;
    display: inline;
    flex-direction: column;
    align-items: center;
    width: 100%;
    border-radius: 10px;
}
.childModal:hover{
    box-shadow: 3px 3px 20px #ddd;
    transition: 0.8s;
}
.childModal p{
    font-family: 'Josefin Sans', sans-serif;
    padding: 1%;
}
.childModal img{
    width: 200px;
    height: 200px;
}