@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200&display=swap');
.thirdSection{
    background-color: #5d2122;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.line{
    width: 10%;
    margin: 1%;
    border: none;
    border-top: solid 2px #fff;
}
.thirdSection p{
    text-align: center;
    color: #fff;
    font-family: 'Manrope', sans-serif;
    font-size: 12px;
}
.pictureDiv{
    display: flex;
    text-align: center;
    color: #fff;
}
.pictureBox img{
    width: 280px;
    height: 230px;
    margin: 60px 60px 20px 60px;
    border-radius: 10px;
    cursor: pointer;
}
.shopButton{
    margin: 2.5%;
    padding: 1% 3% 1% 3%;
    color: #fff;
    width: fit-content;
    color: #fff;
    background-color: #5d2122;
    cursor: pointer;
    font-size: 14px;
    text-decoration: none;
}
.shopButton:hover{
    transition: 0.5s;
    background-color: #fff;
    color: #5d2122;
    border-radius: 20px;
}