.fourthSection{
    background-image: url("../images/banner2.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
    background-attachment: fixed;
    height: 80vh;
    display: flex;
}
.mainDiv{
    background-color: #fff;
    width: 300px;
    height: 300px;
    padding: 2%;
    position: relative;
    left: 60px;
    margin-top: 5%;
    text-align: center;
}
.fourthSection p{
    font-family: 'Brandon Grotesque', 'Regular';
    font-size: 13px;
    color: #5d2122;
    text-align: justify;
}
.fourthSection h1{
    text-align: left;
    margin: 1%;
}
.line2{
    width: 50%;
    border: none;
    border-top: solid 2px #000;
    margin-bottom: 3%;
}
.myButton{
    padding: 4% 10% 4% 10%;
    background-color: #fff;
    color: #5d2122;
    font-family: 'Brandon Grotesque', 'Regular';
    font-size: 13px;
    cursor: pointer;
    text-decoration: none;
    display: flex;
    justify-content: center;
    margin-top: 20px;
}
.myButton:hover{
    transition: 0.5s;
    background-color: #5d2122;
    color: #fff;
    border-radius: 20px;
    box-shadow: 5px 5px 15px #555;
}