@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap');
.videoTag{
  width: 100%;
  height: auto;
  z-index: -10;
}
.videoDiv h1{
  color: #fff;
  font-size: 2.5em;
  position: absolute;
  top: 50%;
  left: 5%;
  transform: translateY(-50%);
  font-family: 'Libre Baskerville', serif;
}
.button{
  position: absolute;
  top: 70%;
  left: 5%;
  padding: 1% 4% 1% 4%;
  background-color: #fff;
  color: #000;
  border: none;
  border-radius: 30px;
}
.button:hover{
  transition: 0.5s;
  color: #fff;
  background-color: #000;
  box-shadow: 5px 5px 15px #f2f2f2;
}
