@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@600&display=swap');
.cryptoDiv{
    display: flex;
    align-items: center;
    margin-left: 1%;
    z-index: 10000;
    width: 100%;
    color: #fff;
}
.cryptoDiv img{
    width: 20%;
    height: auto;
}
.cryptoDiv h6{
    text-transform: uppercase;
    margin-left: 2%;
    margin-right: 2%;
    font-family: 'Raleway', sans-serif;
}
.cryptoDiv h5{
    margin: 0% 2% 0% 2%;
    width: 100%;
}
.cryptoDiv p{
    font-size: 0.8em;
    font-weight: bold;
}
.cryptoDiv:hover{
    transition: 0.5s;
    color: #5d2122;
}
