.firstSectionDiv{
    height: 70vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FCF6ED;
}
.firstSectionDiv p {
    color: #58595B;
    width: 80%;
    margin: 1%;
    font-family: 'Brandon Grotesque', 'Regular';
}
.title{
    padding: 3% 3% 0.6% 3%;
    color: #58595B;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.ruler{
    width: 15%;
    border: none;
    border-top: solid 2px #999;
}
.iconBox{
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-top: 4%;
}
.iconBox img{
    width: 80px;
    height: 80px;
}
  
.iconBox h3{
    color: #58595B;
}
.iconBox img:hover{
    transition: 0.5s;
    width: 90px;
    height: 90px;
    cursor: pointer;
}